import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Joi from "joi";
import axios from "axios";
import { Helmet } from "react-helmet";
import { baseURL } from "../../index.js";

const Login = ({ saveUserData }) => {
  let navigate = useNavigate();
  const [errorList, setErrorList] = useState([]);
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [user, setUser] = useState({
    email: "",
    password: "",
  });

  // Define Joi schema for validation
  const validationLoginForm = Joi.object({
    email: Joi.string()
      .email({ tlds: { allow: false } })
      .required()
      .label("Email"),
    password: Joi.string().min(6).required().label("Password"),
  });

  function getData(eventInfo) {
    let myUser = { ...user };
    myUser[eventInfo.target.name] = eventInfo.target.value;
    setUser(myUser);
  }

  async function sendData() {
    try {
      const { data } = await axios.post(`${baseURL}/auth/login`, user);
      console.log(data);
  
      // Check for different responses from the backend
      if (data.message === "Done") {
        const token = data.token; // Extract the token (without "Bearer")
        setIsLoading(false);
        localStorage.setItem("userToken", token); // Store token in localStorage
        saveUserData(); // Save user data (assuming this function decodes the token)
        navigate("/home"); // Redirect to home page
      } else if (data.message === "Wrong Password") {
        // Handle wrong password or email
        setIsLoading(false);
        setError("Wrong password or email"); // Show the error message for wrong password
      } else {
        // Handle other backend errors (e.g., user not found)
        setIsLoading(false);
        setError(data.message); // Show the backend message
      }
    } catch (err) {
      // Handle network or server errors
      setIsLoading(false);
      setError("An error occurred during login. Please try again.");
      console.error("Login error:", err);
    }
  }
  

  function handleSubmit(event) {
    event.preventDefault(); // Prevent default form submission
    setIsLoading(true); // Set loading state to true

    // Perform validation on user input
    let validation = validationLoginForm.validate(user, { abortEarly: false });

    if (validation.error) {
      setIsLoading(false); // Stop loading if there's a validation error
      setErrorList(validation.error.details);
    } else {
      sendData(); // Call the sendData function
    }
  }

  return (
    <div className="d-flex justify-content-center py-4">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Login</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <form className="form" onSubmit={handleSubmit}>
        <p className="title">Log-In</p>

        {/* Display error messages */}
        {error && <div className="alert alert-danger">{error}</div>}

        {/* Display individual validation errors */}
        {errorList.map((err, index) => (
          <div key={index} className="alert alert-danger">
            {err.message}
          </div>
        ))}

        <input
          placeholder="Your Email"
          className="username input"
          type="email"
          name="email"
          onChange={getData}
          required
        />
        <input
          placeholder="Password"
          className="password input"
          type="password"
          name="password"
          onChange={getData}
          required
        />
        <button className="btn py-2 w-75" type="submit">
          {isLoading ? <i className="fas fa-spinner fa-spin"></i> : "Login"}
        </button>
        <p>
          Don't have an account? <Link to="/signup">Sign-Up</Link>
        </p>
      </form>
    </div>
  );
};

export default Login;
