import React, { useEffect, useState } from "react";
import RootLayout from "../Rootlayout/RootLayout.jsx";
import { createHashRouter, RouterProvider } from "react-router-dom";
import Home from "../Home/Home.jsx";
import Cart from "../Cart/Cart.jsx";
import Login from "../Login/Login.jsx";
import Signup from "../Signup/Signup.jsx";
import Dashboard from "../Dashboard/Dashboard.jsx";
import ProtectedRoute from "../ProtectedRoute/ProtectedRoute.jsx";
import BookDetails from "../BookDetails/BookDetails.jsx";
import { jwtDecode } from "jwt-decode";
import Profile from "../Profile/Profile.jsx";
import OfflineWarning from "../Offline/Offline.jsx";

function App() {
  const [userData, setUserData] = useState(null);
  useEffect(() => {
    const token = localStorage.getItem("userToken");
    if (token) {
      saveUserData(token); // Call saveUserData with the token
    } else {
      console.warn("No token found in localStorage.");
    }
  }, []);
  // Function to save user data
  function saveUserData(token) {
    try {
      const decodedToken = jwtDecode(token); // Decode the token using jwt-decode
      // Combine decoded token data with the token itself
      const userWithToken = {
        ...decodedToken, // Spread decoded token properties
        token, // Add the raw token
      };
      setUserData(userWithToken); // Set the combined data as userData
      console.log("User data with token:", userWithToken); // Debugging purposes
    } catch (error) {
      console.error("Invalid token:", error);
      setUserData(null);
    }
  }
  // Define routes
  let routers = createHashRouter([
    {
      path: "/",
      element: <RootLayout setUserData={setUserData} userData={userData} />,
      children: [
        {
          index: true,
          element: (
            <ProtectedRoute userData={userData}>
              <Home userData={userData} />
            </ProtectedRoute>
          ),
        },
        {
          path: "profile",
          element: (
            <ProtectedRoute userData={userData}>
              <Profile userData={userData} />
            </ProtectedRoute>
          ),
        },
        {
          path: "dashboard",
          element: (
            <ProtectedRoute userData={userData}>
              <Dashboard userData={userData} />
            </ProtectedRoute>
          ),
        },

        {
          path: "home",
          element: (
            <ProtectedRoute userData={userData}>
              <Home userData={userData} />
            </ProtectedRoute>
          ),
        },
        {
          path: "book/:bookId", // Define the route with bookId as a parameter
          element: (
            <ProtectedRoute userData={userData}>
              <BookDetails userData={userData} />
            </ProtectedRoute>
          ),
        },
        {
          path: "login",
          element: <Login saveUserData={saveUserData} />,
        },
        {
          path: "signup",
          element: <Signup />,
        },

        {
          path: "cart",
          element: (
            <ProtectedRoute userData={userData}>
              <Cart userData={userData} />
            </ProtectedRoute>
          ),
        },
      ],
    },
  ]);

  return (
    <>
      <div>
        <OfflineWarning />
        <RouterProvider router={routers} />
      </div>
    </>
  );
}

export default App;
