import React from "react";
import Logo from "../../images/main-logo.png";
import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <header id="header" className="bg-light py-3 border-bottom">
      <div className="container-fluid">
        <div className="row align-items-center">
          {/* Logo Section */}
          <div className="col-6 col-md-2">
            <div className="main-logo">
              <Link to="profile">
                <img src={Logo} alt="logo" className="img-fluid" />
              </Link>
            </div>
          </div>

          {/* Navbar Section */}
          <div className="col-6 col-md-10 d-flex justify-content-end">
            <nav id="navbar" className="d-none d-md-block">
              <div className="main-menu stellarnav">
                <ul className="menu-list list-unstyled d-flex mb-0">
                  <li className="menu-item active me-3">
                    <Link to="home" className="nav-link">
                      Home
                    </Link>
                  </li>
                  <li className="menu-item me-3">
                    <Link to="#" className="nav-link">
                      Featured
                    </Link>
                  </li>
                  <li className="menu-item me-3">
                    <Link to="#" className="nav-link">
                      Popular
                    </Link>
                  </li>
                  <li className="menu-item me-3">
                    <Link to="#" className="nav-link">
                      Offer
                    </Link>
                  </li>
                  <li className="menu-item">
                    <Link to="#" className="nav-link">
                      Articles
                    </Link>
                  </li>
                </ul>
              </div>
            </nav>

            {/* Hamburger Menu for Mobile */}
            <div className="hamburger d-md-none">
              <span className="bar"></span>
              <span className="bar"></span>
              <span className="bar"></span>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Navbar;
