import React, { useState } from "react";
import { Link } from "react-router-dom";

const Header = ({ userData, logout }) => {
  const [isSearchActive, setIsSearchActive] = useState(false); // State to manage search bar visibility

  const toggleSearch = () => {
    setIsSearchActive(!isSearchActive); // Toggle the search bar visibility
  };

  return (
    <div id="header-wrap" className="bg-light py-3 border-bottom">
      <div className="top-content">
        <div className="container-fluid">
          <div className="row justify-content-between align-items-center">
            {/* Left Column - Social Links */}
            <div className="col-12 col-md-6 d-flex align-items-center justify-content-center justify-content-md-start">
              <div className="social-links">
                <ul className="list-inline mb-0">
                  <li className="list-inline-item">
                    <Link
                      to={{ pathname: "https://facebook.com" }}
                      target="_blank"
                      rel="noopener noreferrer"
                      aria-label="Facebook"
                      className="text-dark"
                    >
                      <i className="bi bi-facebook fs-4"></i>
                    </Link>
                  </li>
                  <li className="list-inline-item">
                    <Link
                      to={{ pathname: "http://twitter.com" }}
                      target="_blank"
                      rel="noopener noreferrer"
                      aria-label="Twitter"
                      className="text-dark"
                    >
                      <i className="bi bi-twitter fs-4"></i>
                    </Link>
                  </li>
                  <li className="list-inline-item">
                    <Link
                      to={{ pathname: "http://youtube.com" }}
                      target="_blank"
                      rel="noopener noreferrer"
                      aria-label="YouTube"
                      className="text-dark"
                    >
                      <i className="bi bi-youtube fs-4"></i>
                    </Link>
                  </li>
                  <li className="list-inline-item">
                    <Link
                      to={{ pathname: "http://behance.com" }}
                      target="_blank"
                      rel="noopener noreferrer"
                      aria-label="Behance"
                      className="text-dark"
                    >
                      <i className="bi bi-behance fs-4"></i>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>

            {/* Right Column - User Account, Cart, and Search */}
            <div className="col-12 col-md-6 d-flex justify-content-center justify-content-md-end align-items-center mt-3 mt-md-0">
              <div className="right-element d-flex align-items-center">
                {userData ? (
                  <>
                    <Link to="profile" className="user-account me-3">
                      <i className="bi bi-person-circle fs-5"></i>
                      <span className="ms-2">{userData.userName}</span>
                    </Link>
                    <Link to="cart" className="cart me-3">
                      <i className="bi bi-cart fs-5"></i>
                      <span className="ms-2">{userData.cart}</span>
                    </Link>
                    <i className="bi bi-box-arrow-right fs-5">
                      <span className="ms-1" onClick={logout}>
                        Logout
                      </span>
                    </i>
                  </>
                ) : (
                  <Link to="/login" className="user-account">
                    <i className="bi bi-person-circle fs-5"></i>
                    <span className="ms-2">Login</span>
                  </Link>
                )}

                {/* Search Bar */}
                <div className="action-menu ms-4">
                  <div className="search-bar">
                    {/* <button
                      className="search-button btn btn-link"
                      onClick={toggleSearch}
                      aria-label="Search"
                    >
                      <i className="bi bi-search fs-5"></i>
                    </button> */}

                    {/* Conditionally show/hide search input */}

                    {/* <form
                      role="search"
                      method="get"
                      className={`search-box ms-2 ${
                        isSearchActive ? "d-inline-block" : "d-none"
                      }`}
                    >
                    
                    
                      <input
                        className="search-field form-control form-control-sm"
                        placeholder="Search"
                        type="search"
                        aria-label="Search Input"
                      />
                    </form> */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
