import React, { useState, useEffect } from "react";
import axios from "axios";
import { baseURL, bearerKey } from "../../index.js";
import "./style.css";

export default function BooksManager({ userData }) {
  // State for book details
  const [books, setBooks] = useState([]);
  const [bookForm, setBookForm] = useState({
    bookTitle: "",
    author: "",
    description: "",
    quantity: 0,
    price: "",
    category: "",
    image: null, // For handling image uploads
  });
  const [selectedBook, setSelectedBook] = useState(null); // Book selected for update
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  // Fetch all books from the server
  useEffect(() => {
    const fetchBooks = async () => {
      try {
        const { data } = await axios.get(`${baseURL}/book`);
        setBooks(data.book || []);
      } catch (error) {
        console.error("Error fetching books:", error);
        setError("Failed to fetch books. Please try again later.");
      }
    };
    fetchBooks();
  }, []);
  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value, files } = e.target;
    setBookForm((prevData) => ({
      ...prevData,
      [name]: files ? files[0] : value,
    }));
  };

  // Handle Add/Update book submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    const formData = new FormData();
    formData.append("bookTitle", bookForm.bookTitle);
    formData.append("author", bookForm.author);
    formData.append("description", bookForm.description);
    formData.append("quantity", bookForm.quantity);
    formData.append("price", bookForm.price);
    formData.append("category", bookForm.category);
    if (bookForm.image) formData.append("image", bookForm.image);

    try {
      if (selectedBook) {
        console.log(selectedBook);
        // Update book
        await axios
          .post(`${baseURL}/book/edit/${selectedBook._id}`, formData, {
            headers: {
              authorization: bearerKey + userData.token,
              "Content-Type": "multipart/form-data",
            },
          })
          .then((response) => {
            console.log(response);
          })
          .catch((err) => {
            console.log(err);
          });
        alert("Book updated successfully");
      } else {
        console.log(`adding`);
        // Add new book
        await axios.post(`${baseURL}/book/add`, formData, {
          headers: {
            authorization: bearerKey + userData.token,
            "Content-Type": "multipart/form-data",
          },
        });
        alert("Book added successfully");
      }
      // Refresh the book list
      const { data } = await axios.get(`${baseURL}/book`);
      setBooks(data.book || []);
      // Reset form after submission
      setBookForm({
        bookTitle: "",
        author: "",
        description: "",
        quantity: 0,
        price: "",
        category: "",
        image: null,
      });
      setSelectedBook(null); // Clear form after submission
    } catch (error) {
      console.error("Error saving book:", error);
      setError("Failed to save book. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };
  // Handle Delete book
  async function handleDelete(bookId) {
    try {
      await axios.delete(`${baseURL}/book/delete/${bookId}`, {
        headers: {
          authorization: bearerKey + userData.token,
        },
      });
      alert("Book deleted successfully");
      // Refresh the book list after delete
      const { data } = await axios.get(`${baseURL}/book`);
      setBooks(data.book || []);
    } catch (error) {
      console.error("Failed to delete the book", error);
      setError("Failed to delete book. Please try again.");
    }
  }
  // Handle selecting a book for update
  const handleUpdateClick = (book) => {
    setSelectedBook(book);
    setBookForm({
      bookTitle: book.bookTitle,
      author: book.author,
      description: book.description,
      price: book.price,
      quantity: book.quantity,
      category: book.category,
      image: null, // No image initially
    });
  };

  return (
    <div className="containerAddBook mb-5 pt-3">
      <h2 className="text-center mb-4 text-dark">
        {selectedBook ? "Update Book" : "Add a New Book"}
      </h2>

      {/* Form for adding/updating books */}
      <form className="form m-auto pt-5" onSubmit={handleSubmit}>
        <div className="mb-4">
          <label className="form-label fw-bold">Name of the Book</label>
          <input
            type="text"
            className="form-control shadow-sm rounded-3"
            name="bookTitle"
            value={bookForm.bookTitle}
            onChange={handleInputChange}
            placeholder="Enter the book title"
            required
          />
        </div>
        <div className="mb-4">
          <label className="form-label fw-bold">Author Name</label>
          <input
            type="text"
            className="form-control shadow-sm rounded-3"
            name="author"
            value={bookForm.author}
            onChange={handleInputChange}
            placeholder="Enter the author's name"
            required
          />
        </div>
        <div className="mb-4">
          <label className="form-label fw-bold">Category Name</label>
          <input
            type="text"
            className="form-control shadow-sm rounded-3"
            name="category"
            value={bookForm.category}
            onChange={handleInputChange}
            placeholder="Enter the category"
            required
          />
        </div>
        <div className="mb-4">
          <label className="form-label fw-bold">Description</label>
          <textarea
            className="form-control shadow-sm rounded-3"
            name="description"
            rows="3"
            value={bookForm.description}
            onChange={handleInputChange}
            placeholder="Enter a brief description of the book"
            required
          ></textarea>
        </div>
        <div className="mb-4">
          <label className="form-label fw-bold">Quantity</label>
          <input
            type="number"
            className="form-control shadow-sm rounded-3"
            name="quantity"
            value={bookForm.quantity}
            onChange={handleInputChange}
            placeholder="Enter the quantity"
            required
          />
        </div>
        <div className="mb-4">
          <label className="form-label fw-bold">Price $</label>
          <input
            type="number"
            className="form-control shadow-sm rounded-3"
            name="price"
            value={bookForm.price}
            onChange={handleInputChange}
            placeholder="Enter the price"
            required
          />
        </div>
        <div className="mb-4">
          <label className="form-label fw-bold">Book Image</label>
          <input
            type="file"
            className="form-control bg-light shadow-sm rounded-3"
            name="image"
            onChange={handleInputChange}
          />
        </div>
        {error && <div className="alert alert-danger text-center">{error}</div>}
        <div className="d-flex justify-content-center">
          <button
            className="btn btn-primary shadow-sm rounded-3 w-100"
            type="submit"
            disabled={isLoading}
          >
            {isLoading
              ? selectedBook
                ? "Updating Book..."
                : "Adding Book..."
              : selectedBook
              ? "Update Book"
              : "Add Book"}
          </button>
        </div>
      </form>

      {/* Table of books */}
      <h3 className="mt-5">Books List</h3>
      <table className="table table-striped mt-3">
        <thead>
          <tr>
            <th>Title</th>
            <th>Author</th>
            <th>Price</th>
            <th>Image</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {books.map((book) => (
            <tr key={book._id}>
              <td>{book.bookTitle}</td>
              <td>{book.author}</td>
              <td>${book.price}</td>
              <td>
                <img src={book.image} alt={book.bookTitle} width="50" />
              </td>
              <td>
                <button
                  className="btn btn-warning me-2 w-100"
                  onClick={() => handleUpdateClick(book)}
                >
                  Update
                </button>
                <button
                  className="btn btn-danger"
                  onClick={() => handleDelete(book._id)}
                >
                  Delete
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
