import axios from "axios";
import { baseURL, bearerKey } from "../../index.js";
import { Link } from "react-router-dom";
import React, { useEffect, useState } from "react";
import "./Cart.css"; // Add a CSS file for custom styling

export default function Cart({ userData }) {
  const [books, setBooks] = useState([]); // State to store books in the cart
  const [loading, setLoading] = useState(true); // Loading state
  const [error, setError] = useState(null); // Error state
  const [orderStatus, setOrderStatus] = useState(null); // Status of the order
  const [deleteStatus, setDeleteStatus] = useState(null); // Status of deletion
  const [showOrderForm, setShowOrderForm] = useState(false); // Toggle for order form visibility
  const [address, setAddress] = useState(""); // User's delivery address
  const [phone, setPhone] = useState(""); // User's phone number
  const [note, setNote] = useState(""); // Additional notes for the order
  const [paymentType, setPaymentType] = useState("card"); // Payment type selection

  useEffect(() => {
    // Fetch books from the cart when userData changes
    if (userData) {
      axios
        .get(`${baseURL}/cart`, {
          headers: {
            authorization: bearerKey + userData.token, // Send token in headers
          },
        })
        .then((response) => {
          if (response.data.cart && response.data.cart.books) {
            setBooks(response.data.cart.books); // Set the books in state
            console.log(books);
            
          } else {
            setError("No books found in the cart."); // Handle case with no books
          }
          setLoading(false); // Update loading state
        })
        .catch((error) => {
          setError("Failed to load cart."); // Error handling
          setLoading(false); // Update loading state
        });
    }
  }, [userData]);

  const handleMakeOrder = async () => {
    try {
      const response = await axios.post(
        `${baseURL}/order`,
        {
          address,
          phone,
          note,
          paymentType,
        },
        {
          headers: {
            authorization: bearerKey + userData.token, // Send token in headers
          },
        }
      );

      const { url } = response.data; // Get the checkout URL from response
      if (url) {
        window.location.href = url; // Redirect to the checkout page
      } else {
        console.error("Checkout URL not received"); // Handle missing URL
      }
      setOrderStatus("Order Created"); // Update order status
    } catch (error) {
      console.error("Error during checkout:", error); // Error handling
    }
  };

  const handleDeleteBook = (bookId) => {
    axios
      .delete(`${baseURL}/cart/${bookId}`, {
        headers: {
          authorization: bearerKey + userData.token, // Send token in headers
        },
      })
      .then(() => {
        // Remove book from the local state
        setBooks(books.filter((book) => book.bookId !== bookId)); // Update book list
        setDeleteStatus("Book removed from cart."); // Update delete status
      })
      .catch(() => {
        setDeleteStatus("Failed to delete book."); // Error handling
      });
  };

  const handleClearCart = async () => {
    try {
      await axios.delete(`${baseURL}/cart/clear`, {
        headers: {
          authorization: bearerKey + userData.token, // Send token in headers
        },
      });
      setBooks([]); // Clear the books in the state
      setDeleteStatus("Cart cleared successfully."); // Update delete status
    } catch (err) {
      console.error("Error clearing cart:", err); // Error handling
      setDeleteStatus(
        "Failed to clear cart. " + (err.response?.data?.message || err.message)
      ); // Update delete status with error message
    }
  };

  // Loading and error states
  if (loading) {
    return <p>Loading books...</p>;
  }
  if (error) {
    return <p className="alert alert-danger">{error}</p>;
  }

  return (
    <div className="cart-container">
      <h2 className="cart-title">Your Cart</h2>
      <div className="row g-4 py-3">
        {books.length > 0 ? (
          books.map((book) => (
            <div
              key={`book-${book.bookId}`}
              className="col-lg-4 col-md-6 col-sm-12"
            >
              <div className="card shadow-sm cart-card">
                <div className="card-body">
                  <h5 className="card-title text-center">
                    {book.bookTitle || "Unknown Title"}
                  </h5>
                  <p className="card-text text-center">
                    <strong>Price: </strong>${book.price || "N/A"}
                  </p>
                  <p className="card-text text-center">
                    <strong>Quantity: </strong>
                    {book.quantity}
                  </p>
                  <div className="cart-buttons">
                    <Link
                      to={`/book/${book.bookId}`}
                      className="btn btn-primary w-100 mb-2"
                    >
                      View Book
                    </Link>
                    <button
                      className="btn btn-danger w-100"
                      onClick={() => handleDeleteBook(book.bookId)}
                    >
                      Remove
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ))
        ) : (
          <p className="empty-cart-text">Your cart is empty.</p>
        )}
      </div>

      {books.length > 0 && (
        <div className="cart-actions">
          {!showOrderForm ? (
            <button
              className="btn btn-success mt-3"
              onClick={() => setShowOrderForm(true)} // Show the form when clicked
            >
              Place Order
            </button>
          ) : (
            <div className="order-form">
              <h4>Enter your details:</h4>
              <div className="mb-3">
                <label
                  htmlFor={`address-${userData.userId}`}
                  className="form-label"
                >
                  Address:
                </label>
                <input
                  type="text"
                  className="form-control"
                  id={`address-${userData.userId}`}
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                  placeholder="Enter your delivery address"
                />
              </div>
              <div className="mb-3">
                <label
                  htmlFor={`phone-${userData.userId}`}
                  className="form-label"
                >
                  Phone:
                </label>
                <input
                  type="tel"
                  className="form-control"
                  id={`phone-${userData.userId}`}
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="Enter your phone number"
                />
              </div>
              <div className="mb-3">
                <label
                  htmlFor={`note-${userData.userId}`}
                  className="form-label"
                >
                  Note (Optional):
                </label>
                <textarea
                  className="form-control"
                  id={`note-${userData.userId}`}
                  value={note}
                  onChange={(e) => setNote(e.target.value)}
                  placeholder="Add any special instructions"
                />
              </div>
              <div className="mb-3">
                <label
                  htmlFor={`paymentType-${userData.userId}`}
                  className="form-label"
                >
                  Payment Type:
                </label>
                <select
                  className="form-select"
                  id={`paymentType-${userData.userId}`}
                  value={paymentType}
                  onChange={(e) => setPaymentType(e.target.value)}
                >
                  <option value="card">Card</option>
                  <option value="cash">Cash</option>
                </select>
              </div>

              <button
                className="btn btn-success mt-3"
                onClick={handleMakeOrder}
              >
                Confirm Order
              </button>
            </div>
          )}
          <button
            className="btn btn-warning mt-3 ms-3"
            onClick={handleClearCart}
          >
            Clear Cart
          </button>
        </div>
      )}

      {orderStatus && <p className="alert alert-info mt-3">{orderStatus}</p>}
      {deleteStatus && (
        <p className="alert alert-warning mt-3">{deleteStatus}</p>
      )}
    </div>
  );
}
